import React from 'react';
import ReservationForm from '../components/ReservationForm';
import Sidebar from '../components/Sidebar';

const Agendamento: React.FC = () => {
  // Dados mockados
  const availableTimes = [
    { id: 1, time: '10:00', available: true },
    { id: 2, time: '11:00', available: true },
    { id: 3, time: '12:00', available: false },
    { id: 4, time: '14:00', available: true },
  ];

  const environments = [
    { id: 1, name: 'Biblioteca', description: 'Ambiente silencioso para leitura', icon: '📚' },
    { id: 2, name: 'Salas de Aula', description: 'Salas para aulas e reuniões acadêmicas', icon: '🖊️' },
    { id: 3, name: 'AlphaLAB', description: 'Espaço para inovação e prototipagem', icon: '💡' },
    { id: 4, name: 'Auditório', description: 'Espaço para palestras e eventos', icon: '🎤' }
  ];

  return (
    <div style={{ display: 'flex' }}>
      <Sidebar />
      <div style={{ marginLeft: '250px', padding: '20px' }}>
        <h1>Agendar Reserva</h1>

        <div style={{ display: 'flex', gap: '20px', flexWrap: 'wrap' }}>
          {environments.map((env) => (
            <div
              key={env.id}
              style={{
                cursor: 'pointer',
                padding: '20px',
                border: '2px solid',
                borderColor: 'gray',
                borderRadius: '8px',
                textAlign: 'center',
                width: '150px',
                transition: 'all 0.3s ease',
              }}
              title={env.description}
            >
              <div style={{ fontSize: '30px' }}>{env.icon}</div>
              <h3>{env.name}</h3>
              <p>{env.description}</p>
            </div>
          ))}
        </div>

        {/* Exibe os horários disponíveis estaticamente */}
        <div>
          <h3>Horários Disponíveis</h3>
          {availableTimes.length > 0 ? (
            <ReservationForm availableTimes={availableTimes} onReserve={() => alert('Reserva realizada com sucesso')} />
          ) : (
            <p>Selecione um ambiente para ver os horários disponíveis.</p>
          )}
        </div>

        {/* Lista de reservas fictícias */}
        <div style={{ marginTop: '30px' }}>
          <h3>Reservas Realizadas</h3>
          <ul>
            <li>
              <div>
                <strong>Data:</strong> 20/11/2024 <strong>Horário:</strong> 10:00
              </div>
              <button onClick={() => alert('Reserva editada com sucesso')}>Editar</button>
              <button onClick={() => alert('Reserva cancelada com sucesso')}>Cancelar</button>
            </li>
            <li>
              <div>
                <strong>Data:</strong> 20/11/2024 <strong>Horário:</strong> 14:00
              </div>
              <button onClick={() => alert('Reserva editada com sucesso')}>Editar</button>
              <button onClick={() => alert('Reserva cancelada com sucesso')}>Cancelar</button>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Agendamento;
